import { Component, OnInit, Inject, ViewChild } from '@angular/core';
import { MatTableDataSource } from '@angular/material';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { BASE_URL } from '../../Base_route';
import { Http, Headers, RequestOptions } from "@angular/http";
import { MatSnackBar, MatSnackBarRef } from '@angular/material';
@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.scss']
})
export class ProductComponent implements OnInit {
  data;
  @ViewChild("table")
  table
  events = {
    update: (obj) => {
      var ob = JSON.parse(JSON.stringify(obj));
      let dialogRef = this.dialog.open(DialogOverviewExampleDialog,
        {
          width: "400px",
          data: { object: ob, edit: true }
        });

      dialogRef.afterClosed().subscribe(result => {
        console.log('The dialog was closed');
        if (result)
          this.table.updateTable();
      });
    }
  }
  constructor(public dialog: MatDialog) {


  }

  openDialog(): void {
    let dialogRef = this.dialog.open(DialogOverviewExampleDialog,
      {
        width: "400px",
        data: { object: {} }
      });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      if (result)
        this.table.updateTable();
    });


  }

  ngOnInit() {
  }

}


@Component({
  selector: 'dialog-overview-example-dialog',
  templateUrl: 'createDialog.html',
})
export class DialogOverviewExampleDialog {
  sucursales
  insumosSelect: any = [{}]
  constructor(
    public dialogRef: MatDialogRef<DialogOverviewExampleDialog>,
    @Inject(MAT_DIALOG_DATA) public data: any, private http: Http, public snackBar: MatSnackBar) {



    this.http.get(BASE_URL + "/api/" + "INSUMO", JSON.stringify({

    })).subscribe((res: any) => {
      console.log(res);
      var body = JSON.parse(res._body);
      this.sucursales = body;
    }, (err) => {
      alert("Problemas");
    })
  }

  onNoClick(): void {
    this.dialogRef.close();
  }

  save() {
    delete this.data.object.insumos
    console.log(this.data.object);
    this.http.post(BASE_URL + "/api/" + "PRODUCTO", {
      data: this.data.object
    }).subscribe((res: any) => {

      var body = JSON.parse(res._body);
      this.data.object = body;
      console.log(body);

      if (body) {

        var i = this.insumosSelect.length - 1;


        var fn = () => {
          if (this.insumosSelect[i].ID_INSUMO) {
            this.http.post(BASE_URL + "/api/" + "PRODUCTO_INSUMO", {
              data: {
                ID_PRODUCTO: body.ID_PRODUCTO,
                ID_INSUMO: this.insumosSelect[i].ID_INSUMO
              }
            }).subscribe((res2) => {
              i--;
              if (i >= 0)
                fn();
              else {
                this.data.complete = true;
                var st = "";

                if (this.data.edit)
                  st = "Editado"
                else
                  st = "Guardado"

                var snackBarRef = this.snackBar.open(st, "", {
                  duration: 3000,
                });
                this.dialogRef.close(this.data);
              }
            }, (err2) => {
              console.log(err2)
            })
          } else {
            i--;
            if (i >= 0) {
              fn();
            } else {
              this.data.complete = true;
              var st = "";

              if (this.data.edit)
                st = "Editado"
              else
                st = "Guardado"

              var snackBarRef = this.snackBar.open(st, "", {
                duration: 3000,
              });
              this.dialogRef.close(this.data);
            }
          }
        }
        fn();




      }

    }, (err) => {
      alert("Problemas");
    })
  }

  checkInsumos() {

    var count = 0;

    for (var i = 0; i < this.insumosSelect.length; i++) {
      if (this.insumosSelect[i].ID_INSUMO) {
        count++;
      } else {
        this.insumosSelect.splice(i, 1);
      }
    }

    if (this.insumosSelect.length == 0)
      this.insumosSelect = [{}]

    if (this.insumosSelect.length <= count)
      this.insumosSelect.push({})



  }

}


