
import { Component, OnInit, Inject, ViewChild } from '@angular/core';
import { MatTableDataSource } from '@angular/material';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { BASE_URL } from '../../Base_route';
import { Http, Headers, RequestOptions } from "@angular/http";
import { MatSnackBar, MatSnackBarRef } from '@angular/material';

@Component({
  selector: 'app-venta',
  templateUrl: './venta.component.html',
  styleUrls: ['./venta.component.scss']
})
export class VentaComponent implements OnInit {

  data;
  @ViewChild("table")
  table
  events = {
    update: (obj) => {
      var ob = JSON.parse(JSON.stringify(obj));
      ob.STATUS = "Cancelado"
      delete ob.monto;
      delete ob.productos;
      if (true) {

        this.http.post(BASE_URL + "/api/" + "VENTA", {
          data: ob
        }).subscribe((res: any) => {
  
          var body = JSON.parse(res._body);
         
          console.log(body);
          if (body) {
            this.table.updateTable();
            var snackBarRef = this.snackBar.open("Cancelado", "", {
              duration: 3000,
            });
            
          }
  
        }, (err) => {
          alert("Problemas");
        })
      }



     


    }
  }
  constructor(public dialog: MatDialog,private http: Http, public snackBar: MatSnackBar) {


  }

  openDialog(): void {
    let dialogRef = this.dialog.open(DialogOverviewExampleDialogVenta,
      {
        width: "400px",
        data: { object: {} }
      });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      if (result)
        this.table.updateTable();
    });


  }

  ngOnInit() {
  }

}


@Component({
  selector: 'dialog-overview-example-dialog',
  templateUrl: 'createDialog.html',
})
export class DialogOverviewExampleDialogVenta {
  sucursales
  sucursales2
  insumosSelect: any = [{}]
  totalMount = 0;
  constructor(
    public dialogRef: MatDialogRef<DialogOverviewExampleDialogVenta>,
    @Inject(MAT_DIALOG_DATA) public data: any, private http: Http, public snackBar: MatSnackBar) {

    this.http.get(BASE_URL + "/api/" + "AREA_VENTA", JSON.stringify({

    })).subscribe((res: any) => {
      console.log(res);
      var body = JSON.parse(res._body);
      this.sucursales2 = body;
    }, (err) => {
      alert("Problemas");
    })


    this.http.get(BASE_URL + "/api/" + "PRODUCTO", JSON.stringify({

    })).subscribe((res: any) => {
      console.log(res);
      var body = JSON.parse(res._body);
      this.sucursales = body;
    }, (err) => {
      alert("Problemas");
    })
  }

  onNoClick(): void {
    this.dialogRef.close();
  }

  save() {
    if (this.data.object.ID_SUCURSAL && this.totalMount) {
      delete this.data.object.insumos
      console.log(this.data.object);
      this.http.post(BASE_URL + "/api/" + "direct", {
        query: "exec dbo.newOrder " + this.data.object.ID_SUCURSAL + ", " + this.totalMount + ", 'creado'"
      }).subscribe((res: any) => {

        var body = JSON.parse(res._body);
        this.data.object = body;
        console.log(body);

        if (body) {

          var i = this.insumosSelect.length - 1;


          var fn = () => {
            if (this.insumosSelect[i].ID_INSUMO) {
              this.http.post(BASE_URL + "/api/" + "VENTA_PRODUCTO", {
                data: {
                  ID_PRODUCTO: this.insumosSelect[i].ID_INSUMO,
                  ID_REPORTE_VENTA: body[0].id
                }
              }).subscribe((res2) => {
                i--;
                if (i >= 0)
                  fn();
                else {
                  this.data.complete = true;
                  var st = "";

                  if (this.data.edit)
                    st = "Editado"
                  else
                    st = "Guardado"

                  var snackBarRef = this.snackBar.open(st, "", {
                    duration: 3000,
                  });
                  this.dialogRef.close(this.data);
                }
              }, (err2) => {
                console.log(err2)
              })
            } else {
              i--;
              if (i >= 0) {
                fn();
              } else {
                this.data.complete = true;
                var st = "";

                if (this.data.edit)
                  st = "Editado"
                else
                  st = "Guardado"

                var snackBarRef = this.snackBar.open(st, "", {
                  duration: 3000,
                });
                this.dialogRef.close(this.data);
              }
            }
          }
          fn();




        }

      }, (err) => {
        alert("Problemas");
      })
    }
  }

  checkInsumos() {

    var count = 0;
    this.totalMount = 0;
    for (var i = 0; i < this.insumosSelect.length; i++) {
      if (this.insumosSelect[i].ID_INSUMO) {

        for (var j = 0; j < this.sucursales.length; j++)
          if (this.sucursales[j].ID_PRODUCTO == this.insumosSelect[i].ID_INSUMO)
            this.totalMount += this.sucursales[j].Precio;
        count++;
      } else {
        this.insumosSelect.splice(i, 1);
      }
    }

    if (this.insumosSelect.length == 0)
      this.insumosSelect = [{}]

    if (this.insumosSelect.length <= count)
      this.insumosSelect.push({})



  }




}


