import { Component, OnInit, Inject, ViewChild } from '@angular/core';
import { MatTableDataSource } from '@angular/material';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { BASE_URL } from '../../Base_route';
import { Http, Headers, RequestOptions } from "@angular/http";
import { MatSnackBar, MatSnackBarRef } from '@angular/material';
@Component({
  selector: 'app-empleados',
  templateUrl: './empleados.component.html',
  styleUrls: ['./empleados.component.scss']
})
export class EmpleadosComponent implements OnInit {

  data;
  @ViewChild("table")
  table
  events = {
    update: (obj) => {
      var ob = JSON.parse(JSON.stringify(obj));
      let dialogRef = this.dialog.open(DialogOverviewExampleDialogEmpleado,
        {
          width: "400px",
          data: { object: ob, edit: true }
        });

      dialogRef.afterClosed().subscribe(result => {
        console.log('The dialog was closed');
        if (result)
          this.table.updateTable();
      });
    }
  }
  constructor(public dialog: MatDialog) {


  }

  openDialog(): void {
    let dialogRef = this.dialog.open(DialogOverviewExampleDialogEmpleado,
      {
        width: "400px",
        data: { object: {} }
      });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      if (result)
        this.table.updateTable();
    });


  }

  ngOnInit() {
  }

}







@Component({
  selector: 'dialog-overview-example-dialog',
  templateUrl: 'createDialog.html',
})
export class DialogOverviewExampleDialogEmpleado {
  sucursales
  constructor(
    public dialogRef: MatDialogRef<DialogOverviewExampleDialogEmpleado>,
    @Inject(MAT_DIALOG_DATA) public data: any, private http: Http, public snackBar: MatSnackBar) {


      this.http.get(BASE_URL + "/api/" + "SUCURSAL", JSON.stringify({
        
            })).subscribe((res: any) => {
              console.log(res);
              var body = JSON.parse(res._body);
              this.sucursales = body;
            }, (err) => {
              alert("Problemas");
            })

     }

  onNoClick(): void {
    this.dialogRef.close();
  }

  save() {

    this.data.object.ID_EMPLEADO = this.data.object.ID_Empleado
    delete this.data.object.ID_Empleado
    if (this.data.object.Nombre && this.data.object.Telefono && this.data.object.Telefono < 99999) {
      console.log(this.data.object);
      this.http.post(BASE_URL + "/api/" + "EMPLEADO", {
        data: this.data.object
      }).subscribe((res: any) => {

        var body = JSON.parse(res._body);
        this.data.object = body;
        console.log(body);
        if (body) {
          this.data.complete = true;
          var st = "";
          if (this.data.edit)
            st = "Editado"

          else
            st = "Guardado"
          var snackBarRef = this.snackBar.open(st, "", {
            duration: 3000,
          });
          this.dialogRef.close(this.data);
        }

      }, (err) => {
        alert("Problemas");
      })
    }
  }



}



