import { Component, OnInit, Input, OnChanges, ViewChild, AfterViewInit } from '@angular/core';
import { translator } from '../../Translator';
import { BASE_URL } from '../../Base_route';
import { MatSort, MatPaginator, MatPaginatorIntl } from '@angular/material';
import { MatTableDataSource } from '@angular/material';
import { Http, Headers, RequestOptions } from "@angular/http";
import { MatSnackBar, MatSnackBarRef } from '@angular/material';
@Component({
  selector: 'app-make-table',
  templateUrl: './make-table.component.html',
  styleUrls: ['./make-table.component.scss']
})
export class MakeTableComponent implements OnInit, OnChanges {
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  data;

  @Input("name")
  name

  @Input("idName")
  idName

  ngAfterViewInit() {


    this.http.get(BASE_URL + "/api/" + this.name, JSON.stringify({

    })).subscribe((res: any) => {
      console.log(res);
      var body = JSON.parse(res._body);
      this.data = body;
      this.dataSource = new MatTableDataSource<Element>(this.data);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
      for (var x in this.data[0]) {
        if (!this.ignore[x]) {
          this.names.push({
            name: x
          });

          this.displayedColumns.push(x);
        }
      }

      this.displayedColumns.push("Acciones");
    }, (err) => {
      alert("Problemas");
    })

  }
  @Input("title")
  title;

  @Input('events')
  events

  @Input('hasView')
  hasView

  @Input('ignore')
  ignore = {}

  @Input('hasAvatar')
  hasAvatar;

  @Input('extraActions')
  extraActions

  @Input('tagOptions')
  tagOptions

  @Input('filterButtons')
  filterButtons

  @Input("ignoreDefaultButtons")
  ignoreDefaultButtons

  @Input("rowEvent")
  rowEvent

  @Input("extra")
  extra

  @Input("isNotEditable")
  isNotEditable

  isInit = false;

  names: Array<any> = [];
  translator = translator;
  constructor(ma: MatPaginatorIntl, private http: Http, public snackBar: MatSnackBar) {
    ma.itemsPerPageLabel = "Items por pagina"
    ma.nextPageLabel = "Pagina siguiente"
    ma.previousPageLabel = "Pagina anterior"

  }

  ngOnInit() {






    if (!this.rowEvent)
      this.rowEvent = () => { }

    if (!this.filterButtons && this.extraActions) {
      this.filterButtons = [];
      for (var i = 0; i < this.extraActions.length; i++)
        this.filterButtons[i] = (var1, v2, v3) => {
          return true;
        }
    }

    if (!this.filterButtons && !this.extraActions) {
      this.filterButtons = [];
    }




    if (this.data) {


      /* for (var x in this.data[0]) {
 
         this.names.push({
           name: x
         });
 
       }*/

    }

  }

  typeOf(dat) {
    return typeof dat;
  }
  ngOnChanges() {



    if (this.data && this.names.length == 0) {


      for (var x in this.data[0]) {

        this.names.push({
          name: x
        });

        this.displayedColumns.push(x);

      }

      this.displayedColumns.push("Acciones");

    }

  }

  update(element) {


    if (this.events)
      this.events.update(element)
  }

  delete(element) {


    /*
     */


    console.log(this.idName);
    var lastDataSource = this.dataSource;
    var temporalDeleted = {};
    var indexDeleted = 0;
    for (var i = 0; i < this.data.length; i++) {
      if (this.data[i][this.idName] == element[this.idName]) {
        temporalDeleted = this.data[i];
        indexDeleted = i;
        this.data.splice(i, 1);
        this.dataSource = new MatTableDataSource<Element>(this.data);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      }
    }
    var snackBarRef = this.snackBar.open("Eliminado", "Cacelar", {
      duration: 3000,
    });
    var isCancelled = false;


    snackBarRef.afterDismissed().subscribe(() => {
      if (!isCancelled)
        this.http.delete(BASE_URL + "/api/" + this.name + "/" + element[this.idName], JSON.stringify({

        })).subscribe((res: any) => {

          var body = JSON.parse(res._body);


        }, (err) => {
          alert("Problemas");
        })
    });


    snackBarRef.onAction().subscribe(() => {
      isCancelled = true;

      this.data.splice(indexDeleted, 0, temporalDeleted);
      this.dataSource = new MatTableDataSource<Element>(this.data);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    });


  }

  view(element) {
    if (this.events)
      this.events.view(element)
  }

  updateTable() {
    this.http.get(BASE_URL + "/api/" + this.name, JSON.stringify({

    })).subscribe((res: any) => {
      console.log(res);
      var body = JSON.parse(res._body);
      this.data = body;
      if (this.displayedColumns.length <= 1) {
        this.displayedColumns = [];
        for (var x in this.data[0]) {
          if (!this.ignore[x]) 
            this.names.push({
              name: x
            });
      

          this.displayedColumns.push(x);
        }
        this.displayedColumns.push("Acciones");
      }


      this.dataSource = new MatTableDataSource<Element>(this.data);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;


    }, (err) => {
      alert("Problemas");
    })
  }


  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // MatTableDataSource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }

  displayedColumns = [];

  dataSource



}

export interface Element {

}




